function getSignal(time = 1, f = 300, fs = 3000) {
    /*
    *      
    * time -  
    * f -  
    * fs -  
    */
    
    let ans = { //  
        signal: [],
        time: [],
    };
    let currentImplement = 0; //   

    while (currentImplement / fs <= time) {
        
        let current = 0;
        if (currentImplement % (fs/2) <= f) {
            current = 1;
        }
        
        ans.signal.push(current);
        ans.time.push(currentImplement / fs);

        currentImplement++;
    }
    return ans;
}

export default getSignal;


